import React, { PureComponent, Suspense } from "react";
import { CloudHostUrl, ErrorUTM } from "../../../constants";
import { ERROR_ERROR, formatUnixTimestamp, isDisplayActive } from "../../../utils";
import { AppUrl, socialwallsUrl } from "../../../constants";
const OnsiteUploadButton = React.lazy(() => import("../UserComponents/OnsiteUploadButton"));
const ErrorMessage = React.lazy(() => import("./Elements/ErrorMessage"));
const ErrorWithBranding = React.lazy(() => import('./Elements/ErrorWithBranding'));

const ErrorType = React.memo(({ error, wall }) => { 
  let err = error.errorWithMessage;
  let editor = window.editor ? true : window.location.pathname.includes('/editor') ? true : window.location.href.includes('?editor');
  const isDisplay = isDisplayActive() || window.location.href.includes("social-wall") || window.location.href.includes("widget.socialwalls");
  const redirctUrl = isDisplay ? socialwallsUrl : AppUrl;
 
  if (err) return err && Object.keys(err).length > 0 ? err.type == "VISITOR_LIMIT_EXCEEDED" ? 
  <Suspense fallback={<></>}><ErrorWithBranding title={'Page Views Limit Exceeded'} description={'Oops! Seems Page Views Limit for the plan exceeded. If you are the owner of this Taggbox account, please click below link to access your account.'} utm={'widgetinactive'} button={`<a href="${redirctUrl}${ErrorUTM}viewlimit" target="_blank" class="tb_error_btn_primary">Update Now</a>`} /></Suspense>
  : 
  err.type == "SildeShow" ? 
  <Suspense fallback={<></>}><ErrorWithBranding title={err?.message ? err.message : 'Wall Not exist'}  utm={'widgetinactive'}  /> </Suspense>
  :
  <Suspense fallback={<></>}><ErrorWithBranding title={err?.message ? err.message : 'Wall Not exist'} description={'Oops! Seems like something went wrong. If you are the owner of this Taggbox account, please click below link to access your account.'} utm={'widgetinactive'} button={`<a href="${redirctUrl}${ErrorUTM}widgetinactive" target="_blank" class="tb_error_btn_primary">Access Now</a>`} /> </Suspense>
  : 
  null
  else if (error.planLimit) 
  return <Suspense fallback={<></>}><ErrorWithBranding title={`You're Running Extra Widgets/Feeds!`} description={'To enjoy uninterrupted services,kindly revoke the additional widget/feed or upgrade your plan.'} utm={'widgetinactive'} button={`<a href="${redirctUrl}${ErrorUTM}feedlimit" target="_blank" class="tb_error_btn_primary">Update Now</a>`} /> </Suspense>

  else if (error.themeError && Object.keys(error.themeError).length) {
    switch (error.themeError.error_code) {
      case 201:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`If the Moderation is ON, approve the posts from the moderation section to make them appear on your widget. Else, there wonâ€™t be any posts on your added feeds.`} utm={'noposts'}/></Suspense>;
      case 202:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Feeds!'} description={`Seems like you have not added any feed. Kindly add feed from your preferred social network`} utm={'nofeeds'}/></Suspense>;
      case 203:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'Your Feed Status is Off!!'} description={`Please <a target="${wall.isEditor ? '_self' : '_blank'}" href="${wall.isEditor ? '#' : `${redirctUrl}wall/index/${wall.Wall.id}${isDisplay ? `?manage-feed=true` : ''}`}" id="wallmanageFeed">Click here</a> to turn the status on! Or add another feed`} utm={'feedinactive'}/></Suspense>;
      case 204:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'Feed Not Authorized!'} description={`It appears that you have not authorized any Feed. Please <a target="_blank" href="${redirctUrl}moderation/index/${wall.Wall.id}" rel="noopener noreferrer">Click here</a> to authorize now!`} utm={'feednotauthorized'}/></Suspense>;
      case 205:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Tweets/Posts!'} description={`Currently, No Tweets/Posts are available on the entered <div class="tb_error_highlight">${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}</div>. The new Tweets/Posts will start appearing here.`} utm={'notweets-posts'}/></Suspense>;
      case 206:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Tweets!'} description={`No Tweets are available on the entered <div class="tb_error_highlight">${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}</div> All the upcoming tweets will appear here. <br>The Tweets are older than your chosen ${error.themeError.older_post === 2 ? 7 : error.themeError.older_post === 1 ? 30 : null} days.`} utm={'notweets-posts'}/></Suspense>;
      case 207:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that all your posts are private. Please go to <a href="${redirctUrl}moderation/index/${wall.Wall.id}" target="_blank" rel="noopener noreferrer">Moderation</a> and make them public.`} utm={'noposts'}/></Suspense>;
      case 208:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that the available posts are older than your chosen ${error.themeError.older_days !== 8 ? error.themeError.older_days + ' Days' : formatUnixTimestamp(error.themeError.older_post)}. All the new posts will start to appear here.`} utm={'posts-days'}/></Suspense>;
      case 209:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that there are no posts available with images or videos. The theme doesnâ€™t support text content.`} utm={'nomedia-content'}/></Suspense>;
      case 211:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`No posts are available on the entered ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}. All Upcoming posts will appear here.`} utm={'noposts'}/></Suspense>;
      case 212:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`Only new posts from Instagram ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days} will appear here. Existing posts from Instagram ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days} can not be retrieved.`} utm={'noinstagram-posts'}/></Suspense>;
      default:
        return ""
    }

  } else return err && Object.keys(err).length > 0 ? <ErrorMessage error={error} /> : null
})
export default class ErrorPage extends PureComponent {
  render() {
    const ErrorImage = { backgroundImage: `url(${CloudHostUrl}app/theme/display/images/nopost.png)` }
    const { error, wall } = this.props
    const queryParams = new URLSearchParams(window.location.search);
    const previewId = queryParams.get('preview');
    const isDisplay = isDisplayActive();
    return (
      <>
        {previewId ? document.body.classList.add(`tb_previw-error`) : null}
        <div className={`tb_error_page tb_error_theme_${(wall != null && wall) ? wall.ThemeRule.themeId : 'wall_error'}`}>
          <div className="tb_error_page_bg" style={ErrorImage}> </div>
          {(wall && Object.keys(wall).length > 0 && wall.UserRule.on_site_upload && wall.UgcSettings.onsite_button_status && wall.UgcSettings.onsite_status && !isDisplay) ? <div className="tb_error_onsite_btn">
            <Suspense fallback={<></>}><OnsiteUploadButton UgcSettings={wall.UgcSettings} id={wall.Wall.id} /></Suspense>
          </div> : null}
          <ErrorType error={error} wall={wall} />
          {/* {errorType(error, wall)} */}
        </div>
      </>
    );
  }
}
